.hero {
  padding: 100px 0 150px;

  &__container {
    position: relative;

    @include flex-all-sb;
  }

  &__text {
    max-width: 620px;

    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 51px;
    max-width: 552px;

    font-weight: 600;
    font-size: 65px;
    line-height: 108%;
    color: #000;
  }

  &__info {
    margin: 0;
    margin-bottom: 50px;
    max-width: 470px;

    font-weight: 400;
    font-size: 25px;
    line-height: 140%;
    color: #000;
  }

  &__text-inner {
    display: flex;
    align-items: center;
    column-gap: 10px;
    flex-wrap: wrap;
  }

  &__button {
    margin-bottom: 25px;

    max-width: 160px;
    max-height: 60px;

    font-weight: 600;
    font-size: 16px;
    line-height: 375%;
    text-align: center;
  }

  &__button-sign {
    padding: 0 42px;

    color: #fff;
    background-color: #ff6336;
  }

  &__button-login {
    padding: 1px 28px;

    color: #000;
    border: 1px solid rgba(0, 0, 0, 0.5);
  }

  &__span {
    font-weight: 400;
    font-size: 15px;
    line-height: 167%;
    color: #000;
  }

  &__image {
    width: 759px;
    position: relative;
    transform: translateX(-55px)
  }
}
