.header {
  padding: 35px 0;

  &__container {
    @include flex-all-sb;
  }

  &__left {
    display: flex;
  }

  &__logo {
    max-width: 130px;
  }

  &__navbar {
    margin-left: 51px;

    display: flex;
    align-items: center;
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__button {
    min-width: 120px;
    max-height: 45px;

    font-weight: 600;
    font-size: 16px;
    line-height: 281%;
    text-align: center;
    color: #000;
  }

  &__button-login {
    padding: 0 36px;

    border: 1px solid rgba(0, 0, 0, 0.5);
  }

  &__button-sign {
    padding: 0 22px;

    color: #fff;
    background-color: #ff6336;
  }
}

.navbar {
  &__list {
    display: flex;
    gap: 42px;
  }

  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 133%;
    color: #000;
  }
}
